$main-color: #014498;
$color-tit: #222;
$color-sub: #666;
$color-white : #fff;
$font-small : 16px;
$font-midume : 19px;
$font-big : 24px;

.inner {
    width: 1200px;
    margin: 0 auto;

    ul {
        display: flex;
        gap: 50px;
        line-height: 90px;
        font-size: $font-big;
        font-weight: 500;
    }
}

.tit {
    font-size: 40px;
    font-weight: 600;
    text-align: center;
    line-height: 1.4;
    font-size: $color-tit;
    border-bottom: 1px solid #222;
    margin-bottom: 30px;
}

.sub {
    font-size: 40px;
    font-size: 1.7rem;
    text-align: center;
    color: $color-sub;
    border-bottom: 1px solid #222;
    margin-bottom: 60px;
}